<template>
	<div class="guideMob">
		<guideMobHeader></guideMobHeader>
		<div class="main">
			<guideMobCollapse></guideMobCollapse>
		</div>
	</div>
</template>
<script setup>
import guideMobHeader from "./components/guideMobHeader.vue"
import GuideMobCollapse from "./components/guideMobCollapse.vue";
import {computed} from "vue";
import {useCheckMobile} from "@/store/isMobile";
import {nuxtTo} from "@/utils/tools";
definePageMeta({
	layout: 'content',
})
const isMobile = computed(()=>{
	return useCheckMobile().getItem()
})
onMounted(()=>{
	watchEffect(()=>{
		if (!isMobile.value){
			nuxtTo('/guide')
		}
	})
})
</script>
<style scoped lang="scss">
.guideMob{
	width: 100%;min-height: 100vh;@include df;flex-direction: column;position: relative;
	.main{padding: 5.3333vw 3.7333vw;}
}
</style>